.title{
    --shadowColor: #0b21b3;
    text-align: center;
    font-size: 3.2rem;
    text-shadow: 0 0 10px rgb(35, 30, 30),
    0 0 20px rgb(25, 21, 21),
    0 0 30px var(--shadowColor),
    0 0 40px var(--shadowColor),
    0 0 50px var(--shadowColor), 0 0 60px var(--shadowColor), 0 0 70px var(--shadowColor);

    text-decoration: none;
    color: #b5f0ff;
    font-family: 'Odibee Sans', cursive;
}

a {
    text-decoration: none;
}